import React, { Component } from 'react';

import { connect } from 'react-redux';

// Dispatch
import { addTask } from '../../actions/TaskStore';

import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

// Import TaskBox Component

const styles = (theme) => ({
	main_root: {
		marginTop: 10,
		flexDirection: 'row wrap',
		padding: '0 20px 0 20px',
		width: '100%',
		flexGrow: 1
	},
	root: {
		padding: theme.spacing(3, 2),
		marginBottom: 5
	},
	textField: {
		width: '100%',
		marginTop: 10,
		marginBottom: 10
	}
});

class TaksBoxes extends Component {
	state = {
		inputText: ''
	};

	render() {
		const { classes, addTask } = this.props;

		return (
			<Grid container className={classes.main_root} spacing={0} justify={'center'}>
				<Grid item xs={12} sm={8} md={5}>
					<Paper className={classes.root}>
						<Typography component="h4"> Input Your Text</Typography>

						<TextField
							id="outlined-adornment-weight"
							className={classes.textField}
							variant="outlined"
							label="Task Message"
							value={this.state.inputText}
							onChange={(e) => {
								this.setState({
									inputText: e.target.value
								});
							}}
							onKeyUp={(e) => {
								if (e.which === 13) {
									const { inputText } = this.state;
									if (inputText === '') {
										alert('Plz Fill');
										return;
									}
									addTask(inputText);
									this.setState({
										inputText: ''
									});
								}
							}}
						/>

						<Button
							variant="outlined"
							size="small"
							color="primary"
							onClick={() => {
								const { inputText } = this.state;
								if (inputText === '') {
									alert('Plz Fill');
									return;
								}
								addTask(inputText);
								this.setState({
									inputText: ''
								});
							}}
						>
							Add Task
						</Button>
					</Paper>
				</Grid>
			</Grid>
		);
	}
}

export default connect(null, {
	addTask
})(withStyles(styles)(TaksBoxes));
