import { combineReducers } from 'redux';

import { ADD_TASK, DEL_TASK } from '../actions/TaskStore';

const TaskStore = (state = [], action) => {
	switch (action.type) {
		case ADD_TASK:
			return [ ...state, action.task ];
		case DEL_TASK:
			return state.filter((data) => data !== action.task);
		default:
			return state;
	}
};

const RootReducer = combineReducers({
	TaskStore
});
export default RootReducer;
