import React, { Component } from 'react';

import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

// Import TaskBox Component
import TaskBox from './TaskBox/TaskBox';

// Dispatch Taskbox
import { delTask } from '../../actions/TaskStore';

const styles = (theme) => ({
	main_root: {
		marginTop: 10,
		flexDirection: 'row wrap',
		padding: '0 20px 0 20px',
		width: '100%',
		flexGrow: 1
	},
	root: {
		padding: theme.spacing(3, 2),
		marginBottom: 10
	}
});

class TaksBoxes extends Component {
	render() {
		const { classes, tasks, delTask } = this.props;

		return (
			<Grid container className={classes.main_root} spacing={0} justify={'center'}>
				<Grid item xs={12} sm={8} md={5}>
					{tasks.map((data, index) => <TaskBox key={index} task={data} deleteData={delTask} />)}
				</Grid>
			</Grid>
		);
	}
}

function mapStateToProps(state) {
	return {
		tasks: state.TaskStore
	};
}

export default connect(mapStateToProps, {
	delTask
})(withStyles(styles)(TaksBoxes));
